















































































import { Component, Vue } from "vue-property-decorator";
import store from "../store";
import router from '../router';

@Component
export default class Navbar extends Vue {
  public name = "navbar"
  get authenticated() {
    return store.state.authenticated;
  }
  get username() {
    return store.state.username;
  }
  logout() {
    store.commit("logout");
    router.push("/");
  }
}
